import { getAllWorks } from "@/libs/api";
import { WorkArticle } from "@/types/types";
import Image from "next/image";
import Link from "next/link";
import styles from "./home.module.css";
const { hero, portfolio, titleList, thumbList } = styles;

export default async function Home() {
  const { data } = await getAllWorks();
  const works: WorkArticle[] = data.contents;

  return (
    <>
      <section className={hero}>
        <h1>カスミのポートフォリオ</h1>
      </section>

      <section className={portfolio}>
        <ul className={titleList}>
          {works.map((work) => (
            <li key={work.id}>
              <article>
                <Link href={`/works/${work.id}`}></Link>
                <h3>{work.title}</h3>
              </article>
            </li>
          ))}
        </ul>

        <ul className={thumbList}>
          {works.map((work) => (
            <li key={work.id}>
              <Link href={`/works/${work.id}`}></Link>
              <Image
                src={work.thumbnail.url}
                width={work.thumbnail.width}
                height={work.thumbnail.height}
                alt={work.title}
              />
            </li>
          ))}
        </ul>
      </section>
    </>
  );
}
